import React, { Component } from 'react'
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import LinearProgress from '@material-ui/core/LinearProgress'
import utils from '../Utils/getCor';

export default class Calculos extends Component {

  somaNotas = () => {
    const { nota1, nota2, nota3, nota4, nota5 } = this.props.notas
    return (Number(nota1) + Number(nota2) + Number(nota3) + Number(nota4) + Number(nota5))
  }

  calcPercentTotal = () => {
    return this.somaNotas()/5
  };

  getAprovacao = () => {
    const { nota1, nota2, nota3, nota4, nota5 } = this.props.notas

    if (nota1 >= 60 
        && nota2 >= 60 
        && nota3 >= 60 
        && nota4 >= 60 
        && nota5 >= 60){
      return "Sim";
    }else{
      return "Não";
    }
  }

  getAprovacaoPeloTotal = () => {
    return this.calcPercentTotal() >= 70? "Sim": "Não"
  }

  render() {
    return (
      <div className='calculos-container'>
        <Card style={{background: utils.getCor(this.calcPercentTotal()),
          width: '100%',
        }}>
          <CardContent>
            <p className="title">{this.somaNotas()} <span className='text-secondary'>(500)</span> </p>
            <p>Nota atual</p>
          </CardContent>
        </Card>

        <Card style={{background: utils.getCor(this.calcPercentTotal()),
          width: '100%',
        }}>
          <CardContent>
            <p className="title">{this.calcPercentTotal()+"%"}</p>
            <p>Percentual total</p>
            <LinearProgress variant="determinate" value={this.calcPercentTotal()} />
          </CardContent>
        </Card>

        <Card style={{background: utils.getCorPorString(this.getAprovacao()),
          width: '100%',
        }}>
          <CardContent>
            <p className="title">{this.getAprovacao()}</p>
            <p>Aprovação pela média (60%)</p>
          </CardContent>
        </Card>

        <Card style={{background: utils.getCorPorString(this.getAprovacaoPeloTotal()),
          width: '100%',
        }}>
          <CardContent>
            <p className="title">{this.getAprovacaoPeloTotal()}</p>
            <p>Aprovação pelo percentual total (70%)</p>
          </CardContent>
        </Card>
        
      </div>
    )
  }
}
