const utils = {
  getCor: function(nota){
    if(nota >= 60){
      return '#08d9d6';
    }else{
      return '#ff2e63'
    }
  },
  getCorPorString: function(resultado){
    if(resultado === 'Sim'){
      return '#08d9d6';
    }else{
      return '#ff2e63'
    }
  }
};

export default utils;