import React, { Component } from 'react';
import './Styles/Styles.css';
import Notas from './Components/Notas';
import Calculos from './Components/Calculos';

export default class App extends Component {
  constructor(){
    super()

    this.state = {
      nota1: 100,
      nota2: 100,
      nota3: 100,
      nota4: 100,
      nota5: 100
    };
  }

  handleChange = (event) =>{
    const { nota1, nota2, nota3, nota4, nota5 } = this.state;
    switch (event.target.id) {
      case 'nota1':
        this.setState({
          nota1: event.target.value,
          nota2: nota2,
          nota3: nota3,
          nota4: nota4,
          nota5: nota5
        })
      break;
      case 'nota2':
        this.setState({
          nota1: nota1,
          nota2: event.target.value,
          nota3: nota3,
          nota4: nota4,
          nota5: nota5
        })
      break;
      case 'nota3':
        this.setState({
          nota1: nota1,
          nota2: nota2,
          nota3: event.target.value,
          nota4: nota4,
          nota5: nota5
        })
      break;
      case 'nota4':
        this.setState({
          nota1: nota1,
          nota2: nota2,
          nota3: nota3,
          nota4: event.target.value,
          nota5: nota5
        })
      break;
      case 'nota5':
        this.setState({
          nota1: nota1,
          nota2: nota2,
          nota3: nota3,
          nota4: nota4,
          nota5: event.target.value
        })
      break;
      default:
        break;
    }
  }

  render() {
    return (
    <div className="App">
      <main>
        <h2>Gerenciador de Notas Bootcamp</h2>
        <Notas onChange={this.handleChange} notas={this.state}/>
        <Calculos notas={this.state}/>
      </main>
    </div>
    )
  }
}
