import React, { Component } from 'react'
import TextField from '@material-ui/core/TextField'

export default class Notas extends Component {
  render() {
    const { nota1, nota2, nota3, nota4, nota5 } = this.props.notas
    return (
      <div className='notas-container'>
        <TextField
          id="nota1"
          label="Módulo 1 - Conceitos"
          type="number"
          variant="filled"
          onChange={this.props.onChange}
          value={nota1}
          InputProps={{ inputProps: { min: 0, max: 100 } }}
          style={{color: "red"}}
        />
        <TextField
          id="nota2"
          label="Módulo 2 - Angular"
          type="number"
          variant="filled"
          onChange={this.props.onChange}
          value={nota2}
          InputProps={{ inputProps: { min: 0, max: 100 } }}
        />
        <TextField
          id="nota3"
          label="Módulo 3 - React"
          type="number"
          variant="filled"
          onChange={this.props.onChange}
          value={nota3}
          InputProps={{ inputProps: { min: 0, max: 100 } }}
        />
        <TextField
          id="nota4"
          label="Módulo 4 - Vue"
          type="number"
          variant="filled"
          onChange={this.props.onChange}
          value={nota4}
          InputProps={{ inputProps: { min: 0, max: 100 } }}
        />
        <TextField
          id="nota5"
          label="Módulo 5 - Desafio"
          type="number"
          variant="filled"
          onChange={this.props.onChange}
          value={nota5}
          InputProps={{ inputProps: { min: 0, max: 100 } }}
        />
      </div>
    )
  }
}
